import axios from 'axios';

/*
var config = require('../../config');


export const baseUrl = process.env.NODE_ENV === 'uat' ? config.uat.env.BASE_URL
    : (process.env.NODE_ENV === 'testing' ? config.test.env.BASE_URL
        : (process.env.NODE_ENV === 'development' ? config.dev.env.BASE_URL : config.build.env.BASE_URL));

*/

export const Axios = axios.create({
  baseURL: "../",
  timeout: 20000,
  headers: {
    'Content-Type': 'application/json',
  },
});


export default {
  install (Vue) {
    Vue.prototype.$http = Axios;
  },
};



