import Vue from 'vue';
import Vuex from 'vuex';
import menu from './modules/menu';

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    menu,
  },

  /**
  getters: {
    basePath: state => state.menu.basePath,
    groupName: state => state.menu.groupName,
    defines: state => state.menu.defines,
    moduleList: state => state.menu.moduleList,
  },*/
});

export default store;



