<template>
    <el-menu
      background-color="#337ab7"
      text-color="#fff"
      active-text-color="#ffd04b"
      style="border:0px;"
      @select="selectMethod"
      @open="handleOpen"
      @close="handleClose">

      <el-submenu v-for="(module, mIndex)  in moduleList" :index="mIndex+''" :key="mIndex+''">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>{{mIndex+1}} {{module.name}}</span>
        </template>
        <template v-if="module.apiList.length>1">
          <el-menu-item-group   @click="selectGroup(api)" v-for="(api, apiIndex) in module.apiList" :index="mIndex+'-'+apiIndex" :key="mIndex+'-'+apiIndex">
            <template slot="title"><span style="font-weight: bold;color:darkorange;">
              {{(mIndex+1)+'.'+(apiIndex+1)}} {{api.name}}</span>
            </template>
            <el-menu-item   v-for="(method,mdIndex) in api.methodList" :index="mIndex+'-'+apiIndex+'-'+mdIndex" :key="mIndex+'-'+apiIndex+'-'+mdIndex">
              {{(mIndex +1)+'.'+(apiIndex+1)+'.'+(mdIndex+1)}} {{method.name}}
            </el-menu-item>
          </el-menu-item-group>
        </template>
        <template v-else-if="module.apiList.length==1">
          <el-menu-item   v-for="(method,mdIndex) in module.apiList[0].methodList" :index="mIndex+'-'+0+'-'+mdIndex" :key="mIndex+'-'+0+'-'+mdIndex">
            {{(mIndex +1)+'.'+(mdIndex+1)}} {{method.name}}
          </el-menu-item>
        </template>
      </el-submenu>

    </el-menu>


</template>

<script>
export default {
  name: 'smenu',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
    };
  },
  computed: {
    moduleList () {
      return this.$store.state.menu.moduleList;
    },
  },
  methods: {
    handleOpen (key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      // console.log(key, keyPath);
    },
    selectGroup (api) {
      console.log(api);
    },
    selectMethod (key, keyPath) {
      console.log(1);

      /*
      var ps = key.split("-");
      const mIndex = 0;
      const apiIndex = 1;
      const methodIndex = 2;
      const method = this.moduleList[ps[mIndex]].apiList[ps[apiIndex]].methodList[ps[methodIndex]];
       */
      this.$router.push({ path: '/method/' + key });
    },
  },
};
</script>

