const user = {
  namespaced: true,
  state: {
    config: {},
    defines: {},
    moduleList: [],
  },
  mutations: {
    SET_CONFIG: (state, v) => {
      state.config = v;
    },
    SET_DEFINES: (state, v) => {
      state.defines = v;
    },
    SET_MODULE_LIST: (state, v) => {
      state.moduleList = v;
    },
  },
  actions: {
    SET_CONFIG: (context, config) => {
      context.commit('SET_CONFIG', config);
    },
    SET_DEFINES: (context, v) => {
      context.commit('SET_DEFINES', v);
    },
    SET_MODULE_LIST: (context, v) => {
      context.commit('SET_MODULE_LIST', v);
    },
  },
};

export default user;



