import Vue from 'vue';
import Router from 'vue-router';
import index from '@/modules/index';
import method from '@/modules/method';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      children: [{
        path: "/method/:x",
        component: method,
        props: true,
      }],
    },
  ],
});



