<template>
  <el-card>
    <div slot="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{m.name}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{api.name}}</el-breadcrumb-item>
        <el-breadcrumb-item><span style="color:rgb(171, 15, 58);">{{md.name}}</span></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div>
      <el-row v-if=" md.markDown && config.markDown>0 ">
        <el-col :span="24">
          <mavon-editor :value="markDownStr" :subfield="false"  defaultOpen="preview"   :toolbarsFlag="false" :boxShadow="false" :editable="false" :ishljs="true"/>
        </el-col>
      </el-row>
      <el-row v-if="!(md.markDown&&config.markDown==1)">
        <el-col :span="24"><span class="k_n">接口地址：</span><span class="k_v">{{md.path}}</span></el-col>
        <el-col :span="24"><span class="k_n">请求方式：</span><span class="k_v">{{md.type===null?"不限":md.type}}</span>
        </el-col>
        <el-col :span="24"><span class="k_n">接口说明：</span>
          <div class="k_v" v-html="md.note"></div>
        </el-col>
        <el-col :span="24"><span class="k_n">请求参数：</span></el-col>
        <el-col :span="24">
          <tree-table :data="requestParams" :columns="requestColumns" border/>
        </el-col>
        <el-col :span="24"><span class="k_n">响应参数：</span></el-col>
        <el-col :span="24">
          <tree-table :data="responseParams" :columns="responseColumns" border/>
        </el-col>
      </el-row>
    </div>
  </el-card>

</template>

<script>

import treeTable from '@/components/TreeTable';

export default {
  name: 'method',
  components: { treeTable },
  props: ['x'],
  data () {
    return {
      requestColumns: [
        {
          text: '名称',
          value: 'name',
          width: 300,
        },
        {
          text: '类型',
          value: 'type',
          width: 200,
        },
        {
          text: '数据来源',
          value: 'from',
          width: 200,
        },
        {
          text: '说明',
          value: 'note',
        },
      ],
      responseColumns: [
        {
          text: '名称',
          value: 'name',
          width: 300,
        },
        {
          text: '类型',
          value: 'type',
          width: 200,
        },
        {
          text: '说明',
          value: 'note',
        },
      ],
      markDownStr: "",
    };
  },
  updated () {
    this.tryLoadMarkDown();
  },
  created () {
    this.tryLoadMarkDown();
  },
  computed: {
    responseParams () {
      var data = [];
      if (this.md.returnType) {
        var typeList = this.resolveResponseType(this.$store.state.menu.defines, {}, this.md.returnType);
        this.mergeData(data, typeList);
        data = typeList;
      }

      if (this.md.resultList) {
        var paramList = this.resolveParamType(this.$store.state.menu.defines, this.md.resultList);
        this.mergeData(data, paramList);
      }
      return data;
    },
    requestParams () {
      var data = [];
      if (this.md.paramType) {
        var typeList = this.resolveResponseType(this.$store.state.menu.defines, {}, this.md.paramType);
        this.mergeData(data, typeList);
      }

      if (this.md.paramList) {
        var paramList = this.resolveParamType(this.$store.state.menu.defines, this.md.paramList);
        this.mergeData(data, paramList);
      }
      return data;
    },
    m () {
      var ps = this.x.split("-");
      const mIndex = 0;
      var module = this.$store.state.menu.moduleList[ps[mIndex]];
      if (module) {
        return module;
      }
      return {};
    },
    api () {
      var ps = this.x.split("-");
      const mIndex = 0;
      const apiIndex = 1;
      var module = this.$store.state.menu.moduleList[ps[mIndex]];
      if (module) {
        var api = module.apiList[ps[apiIndex]];
        if (api) {
          return api;
        }
      }
      return {};
    },
    md () {
      var ps = this.x.split("-");
      const mIndex = 0;
      const apiIndex = 1;
      const methodIndex = 2;
      var module = this.$store.state.menu.moduleList[ps[mIndex]];
      if (module) {
        var api = module.apiList[ps[apiIndex]];
        if (api) {
          var md = api.methodList[ps[methodIndex]];
          return md;
        }
      }
      return {};
    },
    config () {
      return this.$store.state.menu.config;
    },
  },
  methods: {
    tryLoadMarkDown () {
      if (this.md.markDown) {
        var that = this;
        that.$http.get('/japi/' + this.md.markDown, {}).then((res) => {
          that.$data.markDownStr = res.data;
        });
      }
    },
    resolveDefineType (dfs, f, type) {
      var re = [];
      var typeInfo = dfs[type];
      if (!typeInfo) {
        console.log("未识别类型:" + type);
        return re;
      }

      // 1 处理基本类型
      if (typeInfo.type && !typeInfo.type.startsWith("$")) {
        // 不是$开头的，说明是基本类型或不可再分类型
        // 由于是基本类型的属性，所以不向下扩展和新建，直接设置到传入f中即可。
        f.type = typeInfo.type;
        let str = "";
        if (f.note) {
          str = str + f.note;
        }
        if (typeInfo.note) {
          str = str + typeInfo.note;
        }
        f.note = str;
        return;
      }

      // 2 处理数组
      if (type.startsWith("java.util.List") || type.endsWith("[]")) {
        f.name = f.name + "[]";
        var otp = this.resolveResponseType(dfs, f, typeInfo.type.replace("$", ""));
        if (otp instanceof Array) {
          f.type = f.type + "[数组]";
          return otp;
        } else if (otp) {
          f.type = otp;
          console.log("解析出来不是数组:" + f.type);
        }

        f.type = f.type + "[数组]";

        return;
      }

      // 3 处理对象
      // f.type = "对象";
      f.type = "";// 由于是对象类型，但没具体名称，所以清理掉
      if (typeInfo.properties.length > 0) {
        // 属性方式描述类型
        for (var i = 0; i < typeInfo.properties.length; i++) {
          var pro = typeInfo.properties[i];
          var temp = {name: pro.name, note: pro.note, type: ""};
          var cp = this.resolveDefineType(dfs, temp, pro.type.replace("$", ""));
          if (cp instanceof Array) {
            temp.children = cp;
          }
          re.push(temp);
        }
      }
      return re;
    },
    resolveResponseType (dfs, f, type) {
      return this.resolveDefineType(dfs, f, type);
    },
    resolveParamType (dfs, ml) {
      var re = [];
      for (var i = 0; i < ml.length; i++) {
        var p = ml[i];
        var t = {name: p.name, note: p.note, from: p.from, type: p.type};

        if (t.type && t.type.startsWith("$")) {
          t.children = this.resolveDefineType(dfs, t, t.type.replace("$", ""));
        }

        if (t.name.endsWith("[]")) {
          t.type = t.type + "[数组]";
        }

        if (p.properties.length > 0) {
          t.children = this.resolveParamType(dfs, p.properties);
        }
        re.push(t);
      }
      return re;
    },
    mergeData (target, data) {
      if (target instanceof Array) {
        // 遍历数组
        for (var index in data) {

          var oldName = data[index].name;
          // 判断目标数组中是否存在
          var tValue = this.findInArrayByName(target, oldName);
          if (tValue === null) {
            // 找不到可能是由于名称类型不一样引起的
            if (oldName.endsWith("[]")) {
              // 尝试删除
              this.removeFromArrayByName(target, oldName.replace("[]", ""));
            }
            // 如果不存在，则直接加入到目标数组中
            target.push(data[index]);


          } else {
            // 如果存在，继续递归合并
            this.mergeData(tValue, data[index]);
          }
        }
      } else if (data.name) {
        if (data.name.endsWith("[]")) {
          // 类型纠正，这是一个数组，删除旧的属性
          target.removeAttribute(data.name.replace("[]", ""));

        }
        for (var pName in data) {
          if (target.hasOwnProperty(pName) && target[pName]) {
            // 如果存在,并且有值，继续递归合并
            this.mergeData(target[pName], data[pName]);
          } else {
            target[pName] = data[pName];
          }
        }
      }
      return target;
    },
    findInObjectByName (data, k) {
      for (var n in data) {
        if (n === k) {
          return data[n];
        }
      }
      return null;
    },
    findInArrayByName (data, k) {
      for (var n in data) {
        if (data[n].name === k) {
          return data[n];
        }
      }
      return null;
    },
    removeFromArrayByName (data, k) {
      var i = 0;
      for (var n in data) {
        if (data[n].name === k) {
          data.splice(i, 1);
          return;
        }
        i++;
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .k_n {
    float: left;
    margin: 10px 15px 10px 5px;
    font-weight: bold;
    width: 70px;
    line-height:20px;
  }

  .k_v {
    float: left;
    margin: 10px 15px 10px 5px;
    color: rgb(171, 15, 58);
    line-height:20px;
  }
</style>



